#root {display: flex;
align-items: center;
justify-content: center;
}

body {
overflow: hidden;

}

.container {
display: block;
width: 100vw;
}

.disp {
   height: 100vh;
    width: 100%;   overflow-y: scroll;
}

.podcasts {
    width: 100%;
    margin: 0 auto;
    
}


#sel {

overflow-x: scroll;

}

.main {
    min-width: 300px;
    height: 100vh;
    padding: 0 8px 8px;
    margin: none;
    box-shadow: 0px 2px 1px -1px rgba(0,0,0,0.2),0px 1px 1px 0px rgba(0,0,0,0.14),0px 1px 3px 0px rgba(0,0,0,0.12);
    display: flex;
    flex-direction: column;
    width: auto;    position: relative;
}

.none, .nill {
    display: none;
}


#back {display: block;}



@media all and (min-width: 799px) {
    
    .container {
        display: flex;
        width: 100vw;
        }
       
      #sel {

overflow-x: hidden;

}  .none {
            display: flex;
        } 
        .disp {
            height: 100vh;
            overflow-y: scroll;
            width: 70%;  
        }
        .nill {
            display: block;
        } 
        .main {
            width: 30%;
            max-width: 500px;
            margin: 0 2px 2px 0;
        }
       #back {display: none;} 
        .podcasts {
    width: 80%;
    margin: 0 auto;
}
}
